@import url(https://fonts.googleapis.com/css?family=Exo+2:400,700);
body {
  margin: 0;
  font-family: "Exo 2", sans-serif;
  color: #4a4a4a;
  font-size: 14px; }

.header {
  color: #fff; }
  .header .mobile {
    height: 200px;
    background-image: -webkit-linear-gradient(110deg, #2b5876 0%, #4e4376 100%);
    background-image: linear-gradient(-20deg, #2b5876 0%, #4e4376 100%); }

.panel {
  padding: 20px;
  border-bottom: 1px solid #e4e4e4; }
  .panel:last-child {
    border-bottom: none; }
  .panel .title {
    color: #333;
    margin: 0; }
  .panel span.subtitle {
    font-size: 0.9em; }
  .panel p {
    line-height: 1.4em; }

